#schedule{
    bottom: 0;
    z-index: 10;
    background: #fff;
    header{
        height: 44px;
        position: relative;
        line-height: 44px;
        text-align: center;
        color: #34d2ee;
        #demo2{
            padding: 0;
            color: #34d2ee;
            top: 10px;
            right: -8px;
            border: none;
        }
        .icon-fanhui{
            font-size: 20px;
            position: absolute;
            left: 10px;
            top: 0;
        }
        .centent{font-size: 20px;}
        .right{
            position: absolute;
            top: 0;
            right: 10px;
        }
    }
    .mui-content-padded{
        #demo2{display: none;}
        .icon-xiala{
            padding-left: 5px;
            font-size: 20px;
        }
    }
    .courceBox{
        position: relative;
        #bg{
            position: absolute;
            width: 100%;
            height: 100%;
            opacity: 0.8;
        }
        .list{
            display: flex;
            text-align: center;
            li{
                flex: 1;
                min-height: 50px;
                line-height: 50px;
                position: relative;
                #itemBg{
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 100%;
                    height: 100%;
                    padding: 10px 0;
                    line-height: 20px;
                    word-break: break-word;
                    display: flex;
                    align-items: center;
                    background: palevioletred;
                    color: #fff;
                    border-radius: 10px;
                }
                .date,
                .time{
                    position: absolute;
                    font-size: 12px;
                    font-weight: bold;
                }
                .date{top:-5px;right: 2px;}
                .time{bottom: -5px;left: 2px;}
                &:nth-of-type(1){
                    flex: none;
                    width: 38px;
                    border: 0.5px solid rgba(#598eb9,0.6);
                    background-color: rgba(32, 211, 243, 0.1);
                }
            }
        }
        .list:nth-of-type(1){
            background-color: rgba(#20d3f3,0.6);
            li{
                min-height: 60px;
                line-height: 33px;
                display: flex;
                justify-content: center;
                align-items: center;
                border: 0.3px solid rgba(#598eb9,0.3);
                .dateweek{
                    font-weight: bold;
                    p{color: #000;}
                    p:nth-of-type(1){font-size: 16px;}
                    p:nth-of-type(2){font-size: 12px;}
                }
            }
        }
    }
}